export default {
  layout: "Adm-content",
  data() {
    return {
      form_number: "",
      student_name: "",
      department: "",
      department_list: "",
      batch: "",
      roll: "",
      reg_code: "",
      department: "",
      batch_list: "",
      receipt_no: "",
      error: "",
      department_name: null,
      form_info: "",
      show: false,
    };
  },
  mounted() {
    this.getDepartment();    
  },
  methods: {
    searchForm() {
      this.$axios
        .$get("/admission/form-search/" + this.form_number)
        .then((response) => {
          this.form_info = "";
          this.show = true;
          
        })
        .catch((error) => {
          if (error.response.status == 302) {
            this.$toaster.error("Form not Available");
            this.form_info = error.response.data.form_info;
            // return false;
            // this.$router.push(`print-receive/${this.form_number}`);
          }
          console.log(error);
          this.error = error.response.data.errors;
        });
    },
    getDepartment() {
      this.$axios
        .$get("/admission/department")
        .then((response) => {          
          this.department_list = response;
        })
        .catch((error) => {
          this.errors = error.response.message.errors;
        });
    },
    getBatch() {
      
      this.$axios
        .$get("/admission/batch/" + this.department)
        .then((response) => {
          console.log(response);
          this.batch_list = response.data;
        })
        .catch((error) => {
          this.error = error.response.data.errors;
        });
    },
    form_sales() {
      let form = {
        student_name: this.student_name,
        department: this.department,
        batch: this.batch,
      };
      this.$axios
        .$post("/admission/form-sales/" + this.form_number, form)
        .then((response) => {
          this.$toaster.success(response.message);
          this.student_name = "";
          this.department = "";
          this.batch = "";
          this.receipt_no = response.receipt_no;
        })
        .catch((error) => {
          this.error = error.response.data.errors;
        });
    },
    print_recieve() {
      this.$axios
        .$get("/admission/print-receive/" + this.form_number, {
          responseType: "blob",
        })
        .then((response) => {
          const url = window.URL.createObjectURL(new Blob([response]));
          const link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", "admission_slip.pdf");
          document.body.appendChild(link);
          link.click();
        })
        .catch((error) => {
          this.error = error.response.data.errors;
        });
    },
  },
};
